body {
  margin: 0;
  width: 100%;
}

.background-img {
  width: 100%;
  position: relative;
}

.background-img > img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: auto;
    z-index: -1;
    opacity: 0.4;
}

body > #header {
  margin-bottom: 0.5%;
  text-align: center;
  font-size: 36px;
  font-family: monospace;
  -webkit-text-fill-color: gold;
  -webkit-text-stroke-color: black;
  text-shadow: 2px 2px black;
  font-weight: 700;
  align-items: center;
  display: flex;
  flex-direction: column; 
}

#heading {
  margin-top: 1.5%;
}

#modal-container {
  position: absolute;
  left: 50% !important;
  top: 50% !important;
  transform: translate(-50%, -50%);
  z-index: 5 !important;
  align-items: center;
  display: flex;
  height: 450px;
  width: 600px;
  box-shadow: 4px 6px black;
  overflow: visible;
  background-color: whitesmoke;
}

#modal {
  display: block;
  text-align: center;
  font-size: 16px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  -webkit-text-fill-color: blue;
  -webkit-text-stroke-color: black;
  text-shadow: 1px 1px black;
  font-weight: 700;
  padding: 5%;
  
  
}

#modal-header {
  padding-bottom: 3%;
  font-size: 20px;
  font-weight: 700;
  -webkit-text-fill-color: black;
}

#runner {
  font-family: 'Courier New', Courier, monospace;
  font-weight: 700;
  font-size: 20px;
  text-shadow: none;
  -webkit-text-fill-color: black;
  align-items: center;
  text-align: center;
  width: 50%;
}

#map {
      height: 450px;
      width: 900px;
      cursor: auto;
      cursor: url(/dist/images/clear.png), auto !important;
      border: solid black 5px;
      margin-top: 10px;
      position: relative;
}

#map > div {
      cursor: auto;
      cursor: url(/dist/images/clear.png), auto !important;
}

#loader {
  border: 10px solid #f3f3f3;
  border-top: 10px solid #3498db;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  text-align: center;
  font-family: monospace;
  -webkit-text-fill-color: black;
  text-shadow: none;
}

#clock-label {
  display: block;
}

#fetch-weather {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: 700;
  color: black !important;
  display: block;
  text-align: center;
  -webkit-animation: no-spin 2s linear infinite;
          animation: no-spin 2s linear infinite;
  margin-top: 8px;
  font-size: 14px;
}
@-webkit-keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
@-webkit-keyframes no-spin {
  0% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
@keyframes no-spin {
  0% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
.score-container {
  text-align: -webkit-center;
  font-weight: 700;
  font-size: 30px;
}
.location {
  align-items: center;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.latitude-container, .longitude-container, #latitude, #longitude {
  text-align: left;
  display: none;
}

.latitude, .longitude {
  display: inline-block;
}

.map-container-inner {
  width: 100%;
  text-align: -webkit-center;
}

#map {
  margin-top: 8%;
}

#main-container, #clock-container {
  display: flex;
  -webkit-text-fill-color: gray;
  -webkit-text-stroke-color: black;
  text-shadow: 2px 2px black;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  margin-bottom: 2%;
  align-items: center;
}

#clock-container {
  justify-content: center;
  font-size: 30px;
  margin-bottom: 1rem;
}

#info-banner {
  width: 45%;
  display: inline-block;
  margin-right: 20%;
  margin-left: auto;
}

#streak-banner {
  width: 45%;
  display: inline-block;
  margin-left: 20%;
  margin-right: auto;
}

#instructions-container {
  display: flex;
  flex-direction: row;
  position: relative;
  height: 100%;
  z-index: 3;
}

#weather-container {
  display: none;
}



#streak-container {
  text-align: -webkit-center;
  font-weight: 700;
  font-size: 30px;
}
#footer {
  width: 100%;
  margin: 0;
  padding-top: 10%;
  box-sizing: border-box;
  bottom: 0;
}

#footer > div {
  text-align: center;
  display: flex;
  flex-direction: column;
  font-family: monospace;
  
}

#footer-name {
  font-size: 18px;
}
#links {
  display: inline-flex;
  justify-content: center;
  height: 100%;
  width: 100%;
  position: relative;
  box-sizing: border-box;
  
}

#links-container {
  position: absolute;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100px;
  width: 100px;
  padding-left: 1rem;
  padding-right: 1rem;
  
}

#links1, #links2, #links3 {
  position: relative;
  width: 20%;
  display: inline-block;
  margin: 0;
  padding: 0;
  
}

#github {
  background-image: url(/dist/images/GitHub_Logo.png);
  background-size: 50px;
  height: 25px;
  width: 50px;
  display: block;
  margin-top: 5px;
}

#linkedIn {
  background-image: url(https://logos-world.net/wp-content/uploads/2020/04/Linkedin-Logo-700x394.png);
  background-size: 50px;
  height: 25px;
  width: 50px;
  
  display: block;
}
#portfolio {
  display: block;
  font-family: monospace;
  font-weight: 700;
  margin-top: 5px;
}

#github, #linkedIn, #portfolio {
  margin-left: 1rem;
  margin-right: 1rem;
}